@import "../../styles/global.scss";

.nav-menu {
  background-color: $color-blue;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  display: none;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  
  &--show {
    position: fixed;
    display: flex;
  }

  .header-row {
    width: 100%;
    top: 0;
    .top-header {
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding: 20px $header-padding;
      .menu {
        padding: 9px;
        border-radius: 10px;
        border: none;
        background-color: transparent;
        cursor: pointer;
        .line {
          width: 1.5rem;
          height: 2px;
          background-color: $color-white;
          margin: 4px 0;
          border-radius: 1px;
        }
        &:hover {
          @include lift-animation;
          background-color: rgba(255, 255, 255, 0.364);
        }
        &:focus {
          @include focus;
        }
      }
    }
  }

  .list-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    .list {
      width: 100%;
      max-width: 50rem;
      text-align: center;
      list-style-type: none;
      padding: 0;
      .link {
        display: block;
        padding: 1.6rem 0;
        -webkit-transition: all 300ms cubic-bezier(0.215, 0.61, 0.355, 1);
        transition: all 300ms cubic-bezier(0.215, 0.61, 0.355, 1);
        color: $color-white;
        font-size: 3rem;
        line-height: 100%;
        font-weight: 700;
        text-decoration: none;
        cursor: pointer;
        &:hover {
          @include lift-animation;
        }
        &:focus {
          @include focus;
        }
      }
    }
  }

  .socials {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    a {
      height: 40px;
      margin: 0 20px;
      &:hover {
        @include lift-animation;
      }
      &:focus {
        @include focus;
      }
    }
  }
}
